@charset "UTF-8";
/* Reset CSS START */
html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
div,
p,
blockquote,
pre,
code,
address,
ul,
ol,
li,
menu,
nav,
section,
article,
aside,
dl,
dt,
dd,
table,
thead,
tbody,
tfoot,
label,
caption,
th,
td,
form,
fieldset,
legend,
hr,
input,
button,
textarea,
object,
figure,
figcaption {
  line-height: 14px;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-weight: normal;
  font-family: "Apple SD Gothic Neo", "애플 SD 산돌고딕 Neo", "Roboto", "Noto Sans KR", sans-serif;
  /* letter-spacing: 0.5px; */
}

html {
  width: 100%;
  height: 100%;
  outline: 0 !important;
  -webkit-text-size-adjust: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.1);
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #222;
  font-size: 10px;
}

ul,
ol,
li,
dl {
  list-style: none;
}

table {
  width: 100%;
  border-spacing: 0;
  border-collapse: collapse;
}

address,
cite,
code,
em {
  font-style: normal;
  font-weight: normal;
}

img,
fieldset {
  border: 0;
}

img {
  display: block;
}

label,
img,
input,
select,
textarea,
button {
  vertical-align: middle;
}

main,
header,
section,
article,
aside,
hgroup,
menu,
nav,
footer,
details,
figcaption,
figure,
details {
  display: block;
}

hr {
  display: none;
}

a {
  color: #222;
  text-decoration: none;
}

a:link,
a:visited,
a:hover,
a:active {
  color: #222;
  text-decoration: none;
  background: none;
}

input,
select,
textarea,
button {
  border: none;
  color: #222;
  background-color: transparent;
  vertical-align: middle;
  -webkit-appearance: none;
  cursor: pointer;
}

textarea {
  overflow: auto;
}

input::-ms-clear {
  display: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-out-spin-button {
  -webkit-appearance: none;
}

select::-ms-expand {
  display: none;
}

select:focus,
textarea:focus,
input:focus {
  border: 0;
}

input::-webkit-input-placeholder,
input:focus::-webkit-input-placeholder,
input[readonly],
input[disabled] {
  -webkit-appearance: none;
}

sup {
  position: relative;
  bottom: 1ex;
}

sub {
  position: relative;
  bottom: -1ex;
}

#wrap {
  width: 100%;
  height: 100%;
}

#wrap {
  width: 100%;
  height: 100%;
  overflow: hidden;
}